<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-lx-calendar" /> </el-breadcrumb-item>
        <el-breadcrumb-item>markdown</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="plugins-tips">
        mavonEditorVuemarkdown
        <a href="https://github.com/hinesboy/mavonEditor" target="_blank">mavonEditor</a>
      </div>
      <mavon-editor ref="md" v-model="content" style="min-height: 600px" @imgAdd="$imgAdd" @change="change" />
      <el-button class="editor-btn" type="primary" @click="submit"></el-button>
    </div>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
  name: 'Markdown',
  components: {
    mavonEditor
  },
  data: function() {
    return {
      content: '',
      html: '',
      configs: {
      }
    }
  },
  methods: {
    // md
    $imgAdd(pos, $file) {
      var formdata = new FormData()
      formdata.append('file', $file)
      // 
      this.$axios({
        url: '/common/upload',
        method: 'post',
        data: formdata,
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then((url) => {
        this.$refs.md.$img2Url(pos, url)
      })
    },
    change(value, render) {
      // render  markdown 
      this.html = render
    },
    submit() {
      console.log(this.content)
      console.log(this.html)
      this.$message.success('')
    }
  }
}
</script>
<style scoped>
    .editor-btn{
        margin-top: 20px;
    }
</style>
